<template>
  <transition name="fade-in">
    <header class="race-header" v-if="!raceDataIsFetching">
      <div class="race-header__top">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 139.01 63.64" v-bind:svg-inline="''" v-bind:class="'race-header__logo'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M129.39 28.46c-36.89-.74-36.4 3.64-33.49 4.26 29.05 6.27 26.13 18.21 5.48 22-2.8.51-37.2 8.36-99.09 7.32l4.18-9.41c58.26-.28 87.41-3.14 89.29-8.36 1.89-5.27-23.45-7.43-23.29-12.55.18-5.66 29.54-13.23 63.64-12.55l-6.72 9.29z" fill="none" stroke="#ED1C24" stroke-width="3" stroke-linecap="square" stroke-miterlimit="10"/><path fill="#fff" class="st1" d="M71.56 3.86l-14 36.75h-8.75L62.66 3.86h8.9m4.36-3H60.58l-.73 1.94-13.84 36.75-1.53 4.06h15.16l.74-1.93 14-36.75L75.92.86zM37.57 3c3.48 0 7.92.13 13.67.18l-2.71 7.1H32.66l-3.55 9.76H43l-2.78 7.1H25.74L21 40.45h-8.59C27.09 6.38 19.88 3 37.57 3m0-3c-6.54 0-10.38.47-13.25 3.01-2.71 2.41-3.91 6.18-5.89 12.42-1.75 5.53-4.16 13.11-8.78 23.84l-1.8 4.19h15.27l.71-1.99 4.03-11.32h14.41l.75-1.91 2.78-7.1 1.6-4.09h-14l1.37-3.76h15.84l.74-1.93 2.71-7.1L55.6.23 51.29.19c-2.68-.02-5.08-.07-7.23-.1C41.58.04 39.44 0 37.57 0z"/></svg>
        <div class="race-header__info">
          <button
            v-if="$navigatorOnline"
            :class="['race-header__race-btn', {'active': raceActive}]"
            v-on:click="activateRaceOverlay(true)"
            ref="heading"
          >
            <h1 class="race-header__race">{{ race.raceName }}</h1>
          </button>
          <span v-else class="race-header__race-btn race-header__race-btn--offline" ref="heading">
            <h1 class="race-header__race">{{ race.raceName }}</h1>
          </span>

          <span class="race-header__round-and-date">
            <span class="race-header__round">Round {{ race.round }},&nbsp;</span>
            <button
              v-if="$navigatorOnline"
              :class="['race-header__year-btn', {'active': yearActive}]"
              v-html="race.season"
              v-on:click="activateYearOverlay"
            ></button>
            <span
              v-else
              class="race-header__year-btn race-header__year-btn--offline"
              v-html="race.season"
            ></span>
            <span class="race-header__date" v-html="seperateDate(race.date)"></span>
          </span>
        </div>
      </div>
      <RaceSelect
        v-if="!latestRaceDataFetching && $navigatorOnline"
        :latestRaceData="latestRaceData"
        :seasons="seasons"
        :race="race"
      ></RaceSelect>
    </header>
  </transition>
</template>

<script>
import RaceSelect from '@/components/RaceSelect'
import EventBus from '@/event-bus/event-bus.js'

export default {
  name: 'race-header',
  components: {
    RaceSelect
  },
  props: {
    race: [Object, Boolean],
    raceDataIsFetching: Boolean,
    seasons: Array,
    latestRaceData: [Object, Boolean],
    latestRaceDataFetching: [Boolean]
  },
  data: () => ({
    headingActive: true,
    yearActive: false,
    raceActive: false
  }),
  mounted () {
    // Emitted from raceSelect.vue
    EventBus.$on('activateRaceOverlay', () => {
      this.activateRaceOverlay()
    })
  },
  methods: {
    seperateDate (e) {
      // if (this.raceDataIsFetching) {
      return e.split(/[0-9]{4}/).pop()
      // }
    },
    activateYearOverlay () {
      this.raceActive = false
      !this.yearActive ? this.yearActive = true : this.yearActive = false

      // Emitted to RaceSelect.vue
      EventBus.$emit('raceSelectYearState')
    },
    activateRaceOverlay (bool) {
      this.yearActive = false
      !this.raceActive ? this.raceActive = true : this.raceActive = false

      if (bool) {
        // This only needs to run if triggered from this component
        // Emitted to RaceSelect.vue
        EventBus.$emit('raceSelectRaceState')
      }
    }
  }
}
</script>

<style scoped>
.fade-in-enter,
.fade-in-leave-to {
  opacity: 0;
  visibility: hidden;
}

.fade-in-enter-active,
.fade-in-leave-active {
  transform: translate3d(0, 0, 0);
  transition: opacity 0.3s ease-in;
}
</style>
